// базовые подключения
@import "zeroing";
@import "mixins";
@import "fonts";
@import "settings";

// подключения компонентов страницы
@import "./components/header";

// тестовые стили, удалите их


.container {
  width: 1170px;
  margin: 0 auto;
}
.footer__container-phone {
  display: none;
}

.header {
  height: 80px;
  background: #3F3446;
  & .header__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 22px 0px;
  }
  & .container__logo {
    display: flex;
    align-items: center;
  }
  & .mobile__header {
    display: none;
  }
  & .container__logo p {
    font-family: 'Ubuntu';
    font-style: normal;
    font-weight: 300;
    font-size: 16px;
    line-height: 140%;
    color: #FFFFFF;
    padding-left: 13px;
  }
  & .list {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  & .item a {
    font-family: 'Ubuntu';
    font-style: normal;
    font-weight: 300;
    font-size: 16px;
    line-height: 140%;
    color: #FFFFFF;
    padding: 0px 19px;
  }
  & .phone p {
    font-family: 'Ubuntu';
    font-style: normal;
    font-weight: 300;
    font-size: 16px;
    line-height: 140%;
    color: #FFFFFF;
  }
}
.fon {
  width: 100%;
  max-height: 823px;
}
.main {
  .section {
    padding-bottom: 120px;
    .background__phone {
      display: none;
    }
  }
  .section:nth-child(1) {
    position: relative;
  }
  .section__background {
    img {
      width: 100%;
      height: 720px;
    }
  }
  .section__block {
    width: 1110px;
    position: absolute;
    top: 340px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .section__block-left {
      position: absolute;
      left: 0px;
    }
    .section__block-right {
      position: absolute;
      right: 0px;
      img:nth-child(1) {
        width: 481px;
        height: 573px;
      }
      img:nth-child(2) {
        display: none;
      }
      img:nth-child(3) {
        display: none;
      }
    }
  }
  & .section-p1 {
    font-family: 'Ubuntu';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 18px;
    text-transform: uppercase;
    color: #212121;
  }
  & .section-p2 {
    font-family: 'Ubuntu';
    font-style: normal;
    font-weight: 300;
    font-size: 16px;
    line-height: 140%;
    color: #212121;
  }
  & .section__title {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  & .section__title {
    padding-bottom: 60px;
  }
  .btn {
    font-family: 'Ubuntu';
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 21px;
    color: #FFFFFF;

    background: linear-gradient(180deg, rgba(84, 95, 196, 0.9) 1.96%, #7834CE 100%);
    border-radius: 5px;
    padding: 17px 77px;
  }
}
.section__block-2 {
  display: flex;
  align-items: center;
  justify-content: space-between;
  .section__background {
    height: 723px;
  }
  .section__block-left {
    img:nth-child(2) {
      display: none;
    }
  }
  .section__block-right {
    padding-left: 30px;
    h2 {
      font-family: 'Ubuntu';
      font-style: normal;
      font-weight: 300;
      font-size: 48px;
      line-height: 55px;
      color: #000000;
      padding-bottom: 60px;
    }
    .block__text {
      display: flex;
      flex-direction: column;
      align-items: start;
      padding-bottom: 40px;
      p {
        width: 100%;
        font-family: 'Ubuntu';
        font-style: normal;
        font-weight: 300;
        font-size: 16px;
        line-height: 140%;
        color: #000000;
        padding-bottom: 30px;
      }
      .p-1 {
        padding-left: 70px;
      }
    }
  }
}

.section__block-3{
  display: flex;
  flex-direction: column;
  
  .text__slider {
    position: relative;
    width: 100%;
    height: 100px;
    .flex-wrapper {
      display: flex;
      width: 1000px;
      height: 100px;
      justify-content: center;
      flex-direction: column;
      overflow: hidden;
      position: absolute;
      top: 0;
      .slider-wrapper {
        position: absolute;
        display: flex;
        left: 80px;
        .slide {
          display: block;
          width: 1000px;
          p {
            height: 68px;
            padding: 0px 25px;
            font-family: 'Ubuntu';
            font-style: normal;
            font-weight: 300;
            font-size: 16px;
            line-height: 140%;
            color: #000000;
  
            transition: padding-left 1s ease-in-out;
          }
        }
      }
    }
  }
  .button__dot {
    display: flex;
    position: relative;
    width: 100%;
    top: -60px;
    #btn-prev, #btn-next {
      cursor: pointer;
      opacity: 0.4;
      transition: opacity .2s ease-in-out;
  
      display: inline-flex;
      width: 20px;
      height: 20px;
    }
    #btn-prev {
      border-top: 3px solid black;
      border-right: 3px solid black;
      transform: rotate(225deg);
      opacity: 0.8;
      position: absolute;
      left: 0;
    }
    #btn-next {
      border-top: 3px solid black;
      border-right: 3px solid black;
      transform: rotate(45deg);
      opacity: 0.8;
      position: absolute;
      right: 0;
    }
  }
  .dots-wrapper {
      display: flex;
      justify-content: center;
      margin-top: 20px;
      .dot {
        margin: 0px 15px;
      }
      .dot.active {
        background-color: rgb(0, 0, 0);
      }

    span {
      width: 40px;
      height: 5px;
      border-radius: 3px;
      background-color: gray;
      margin-right: 10px;
      cursor: pointer;
    }
    span:last-child {
      margin-right: 0px;
    }
  }
}



.section__author {
  .section__block-author {
    .section__block-left {
      padding-right: 30px;
    }
    .section__block-right {
      img:nth-child(1) {
        display: block;
      }
      img:nth-child(2) {
        display: none;
      }
    }
    display: flex;
    justify-content: space-between;
    align-items: center;
    .block__text {
      p {
        width: 100%;
        font-family: 'Ubuntu';
        font-style: normal;
        font-weight: 300;
        font-size: 16px;
        line-height: 140%;
        color: #000000;
      }
      .p {
        padding: 26px 0px 26px 65px;
      }
    } 
  }
}
.section__slider {
  padding-bottom: 20px;
}
.section__life {
  .section__block-4 {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .section__block-left {

  }
  .section__block-right {
    padding-left: 30px;
    p {
      font-family: 'Ubuntu';
      font-style: normal;
      font-weight: 300;
      font-size: 16px;
      line-height: 140%;
      color: #000000;
      padding-bottom: 40px;
    }
  }
}
.section__benefits {
  .section__block-5 {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .block-text {
    display: flex;
    align-items: center;
    flex-direction: column;
    p {
      padding-top: 15px;
      font-family: 'Ubuntu';
      font-style: normal;
      font-weight: 300;
      font-size: 16px;
      line-height: 140%;
      color: #000000;
    }
  }
}

.section__block-6 {
  width: 1170px;
  margin: 0 auto;
  position: relative;
  .container__photo {
    width: 920px;
    overflow: hidden;
    align-self: center;
    margin-left: 120px;
    left: 0;
    .slider__photo {
      width: 100%;
      display: flex;
      position: relative;
      left: 0px;
      transition: all ease 1s;
      .images {
        display: flex;
        justify-content: space-between;
        align-items: center;
        position: relative;
        left: 0;
        width: 1170px;
        .image__slider {
          border-radius: 10px;
        }
      }
    }
  }
  .arrows__photo {
    top: 260px;
    position: absolute;
    display: flex;
    width: 1070px;
    left: 50px;
    .arrows__prev, .arrows__next {
      cursor: pointer;
      opacity: 0.4;
      transition: transform opacity .1s ease-in-out;
    }
    .arrows__prev {
      position: absolute;
      left: 0;
      width: 30px;
      height: 30px;
      border-top: 3px solid black;
      border-right: 3px solid black;
      transform: rotate(225deg);
    };
    .arrows__next {
      position: absolute;
      right: 0;
      width: 30px;
      height: 30px;
      border-top: 3px solid black;
      border-right: 3px solid black;
      transform: rotate(45deg);
    };
    .arrows__prev:hover {
      opacity: 0.8;
      transform: rotate(225deg);
    }
    .arrows__next:hover {
      opacity: 0.8;
      transform: rotate(45deg);
    }
  }
    
}
.image__flex {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr;
  gap: 9px;
  
  .mini__image.active{
    img {
      box-shadow: -1px 1px 3px 1px rgba(173, 0, 255, 0.5);
    }
  }
  .mini__image {
    cursor: pointer;
    transform: translateX(0);
    img {
      border-radius: 5px;
    }
  }
  .mini__image:hover {
    transform: translateX(10px);
    transition: transform 0.6s ease;
  }
}



